@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "DIN Pro";
  src: url("../fonts/DIN_Pro/DINPro.eot");
  src: local("../fonts/DIN_Pro/DIN Pro"), local("DINPro"), url("../fonts/DIN_Pro/DINPro.eot?#iefix") format("embedded-opentype"), url("../fonts/DIN_Pro/DINPro.woff") format("woff"), url("../fonts/DIN_Pro/DINPro.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "DIN Pro Medium";
  src: url("../fonts/DIN_Pro/DINPro-Medium.eot");
  src: local("../fonts/DIN_Pro/DIN Pro Medium"), local("DINPro-Medium"), url("../fonts/DIN_Pro/DINPro-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/DIN_Pro/DINPro-Medium.woff") format("woff"), url("../fonts/DIN_Pro/DINPro-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "DIN Pro Bold";
  src: url("../fonts/DIN_Pro/DINPro-Bold.eot");
  src: local("../fonts/DIN_Pro/DIN Pro Bold"), local("DINPro-Bold"), url("../fonts/DIN_Pro/DINPro-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/DIN_Pro/DINPro-Bold.woff") format("woff"), url("../fonts/DIN_Pro/DINPro-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
